import { defineAsyncComponent, DefineComponent } from 'vue';

export default function loadRemoteModule<PropsOptions>(
  src: string
): DefineComponent<PropsOptions> {
  console.log(`加载组件 src=${src}`)
  return defineAsyncComponent({
    errorComponent: () => '加载错误',
    // loadingComponent: () => '加载中',
    loader: async () => {
      const { default: Component } = await System.import(src);
      return Component;
    },
    onError: (err, retry) => {
      console.error(err);
      console.error(`[loadRemoteModule] 远程组件加载失败, src=${src}`);
    }
  });
}
